.aboutme_text{
    color: darkslategray;
    font: smaller;
    line-height: 22px;
    white-space: pre-line;
    text-align: justify;
}
.rtimeline .timline_cont .timeline_title{
    font-size: 16px;
    font-weight: 500 !important;
}
.rtimeline .timline_cont .timeline_date {
    color: darkslategray;
}
.rtimeline .timline_cont .timeline_desc{
    color: gray;
    font-size: small;
    margin-top: 20px;
}
.service{
    height: 100%;
    text-align: center;
}
.service_icon > .MuiSvgIcon-root {
    font-size: 40px !important;
    margin-left: 0 !important;
    font-weight: 300 !important;
    color: darkslategray;
}
.service .service_title{
    font-size: 16px !important;
    font-weight: 500 !important;
    line-height: 35px !important;
    text-align: center;
}
.service .service_desc{
    color: #989898 !important;
    border-color: var(--main-color) !important;
    margin-right: 5px !important;
    text-align: center;
}